import * as d3 from 'd3';
import { useEffect, useRef, useState } from 'react';
import Next from '../../img/next.svg';
import Back from '../../img/back.svg';
import SearchIcon from '../../img/search.svg';
import { useApp } from '../../AppProvider';

// component to represent a bar in a bar graph
function Bar ({time, i, dashWidth, dashHeight, hourlyMax}) {
  const [showTooltip, setShow] = useState (false);
  let margin = dashWidth / 20;
  let contentWidth = 18 * dashWidth / 20;
  let offset = 0.1 * contentWidth / 24;
  const show = e => setShow (true);
  const hide = e => setShow (false);
  return (
    <g>
      <rect onMouseEnter={show} onMouseLeave={hide} x={margin + offset + i * contentWidth / 24} width={(contentWidth / 24) - (offset / 2)} y={(dashHeight - 20) - 0.66 * dashHeight * time.length / hourlyMax} height={0.66 * dashHeight * time.length / hourlyMax}/>
      {
        showTooltip &&
        <text x={margin + i * contentWidth / 24} y={dashHeight}>{(i % 12) || 12}{i > 11 ? 'PM' : 'AM'}</text>
      }
    </g>
  )
}

// calculate log 2 of x rounded up
const log2 = x => Math.ceil (Math.log(x) / Math.log (2));

// round a date down to begging of day
const roundDown = d => {
  return new Date (d.getFullYear (), d.getMonth (), d.getDate ());
}

// index a list of entries by time of day
const reduceByTime = (acc, entry) => {
  acc [new Date (entry.start).getHours ()].push (entry);
  return acc;
}
const runIndexByTime = entries => entries.reduce (reduceByTime, new Array (24).fill (1).map (() => []));

// index list of entries by entry type
const reduceByType = (acc, entry) => {
  if (acc [entry.entryType]) acc [entry.entryType].push (entry);
  else acc [entry.entryType] = [entry];
  return acc;
}
const runIndexByType = entries => entries.reduce (reduceByType, {});

// extract all text information from an entry
const extractText = (entry) => {
  if (entry.entryType === 'questions') return entry.answers.join (' ');
  if (entry.entryType === 'freeform') return entry.freeform;
  return ''
}

// turn index of entries by day into index of words by day
const reduceIndexedList = idx => {
  return idx.list.map (extractText).join (' ').toLowerCase ();
}
const indexToWordList = entries => entries.map (reduceIndexedList)

// create a word conunt calculator based on entry list
// then pass in a word as an argument to get the count
const getWordCountList = entries => word => {
  let idx = indexToWordList (entries);
  let ret = idx.map ((day, i) => {
    return {
      count: day.split (word.toLowerCase ()).length - 1,
      date: roundDown (entries [i].list [0].start)
    }
  });
  console.log (ret);
  return ret;
}

// general dimensions for svgs
const dashWidth = 750; const dashHeight = 500;

export default function TimeOfDayGraph () {
  const {journal: {entryList, entries}} = useApp ();
  const [metric, setMetric] = useState (0);
  const [indexed, setIndexed] = useState (null);
  const [typeIndex, setTypeIndex] = useState (null);
  const [hourlyMax, setMax] = useState (-1);
  const [keywords, setKeywords] = useState ([]);
  const [tstart, setTouchStart] = useState (null);
  const canvasRef = useRef ();
  const wordcountref = useRef ();
  
  // process entry list on change
  useEffect (() => {
    let idx = runIndexByTime (entryList);
    setMax (idx.reduce ((acc, val) => val.length > acc ? val.length : acc , 1) ) 
    setIndexed (idx);
    setTypeIndex (runIndexByType (entryList));
  }, [entryList]);
  
  // create pie chart when the canvas is defined or metric changes
  useEffect (() => {
    if (!canvasRef.current) return;
    // clear previous data
    let svg = d3.select (canvasRef.current);
    svg.selectAll ('*').remove ();
    if (metric !== 1) return;
    // initialize data
    const colors = ['#4b4486', '#CA054D', '#A4D4B4', '#ffcf9c'];
    let dataset = Object.keys (typeIndex).map (k => Object.create ({
      entryType: k,
      percent: 100 * typeIndex [k].length / entryList.length
    }));
    // generate pie chart
    let pie = d3.pie ().value (d => d.percent);
    let arc = d3.arc ().outerRadius (dashHeight / 3).innerRadius (dashHeight / 4);
    let g = svg.append ('g');
    g.attr ('transform', `translate(${dashWidth / 2}, ${dashHeight / 2})`);
    let arcs = g.selectAll ('arc').data (pie (dataset)).enter ().append ('g').attr ('class', 'arc');
    arcs.append ("path").attr ('fill', (d, i) => colors [i]).attr ("d", arc);
    // generate legend
    let legend = svg.append ('g').attr ('class', 'legend').attr ('transform', `translate(${1.25 * dashWidth / 3}, ${0.85 * dashHeight / 3})`)
    const innerSize = .9 * dashWidth / 3;
    dataset.forEach (({entryType}, i) => {
      legend.append ('circle').attr ('r', 5).attr ('cx', innerSize / 8).attr ('cy', (i + 0.5) * innerSize / dataset.length).attr ('fill', colors [i])
      legend.append ('text').text (entryType).attr ('x', 10 + innerSize / 8).attr ('y', 3.5 + (i + 0.5) * innerSize / dataset.length);
    });
  }, [canvasRef.current, metric]);

  // methods for mutating keywords

  // remove a keyword
  const removeKeyword = word => e => setKeywords (keywords.filter (w => w !== word));

  // add a keyword
  const addKeyWord = e => {
    e.preventDefault ();
    if (keywords.length > 3) return;
    setKeywords ([...keywords, e.target.elements.keyword.value]);
    e.target.elements.keyword.value = '';
  }
  // hook for updating word count dict
  useEffect (() => {
    if (!entries || !entries.length) return;
    // select the svg
    let svg = d3.select (wordcountref.current);
    if (!svg) return;
    svg.selectAll ('*').remove ();
    // calculate the word counts
    let count = getWordCountList (entries);
    let lists = keywords.reduce ((acc, val) => {
      return Object.assign (acc, {
        [val]: count (val)
      })
    }, {})
    // create axes domain-range pairs
    const xDomain = [
      roundDown (new Date (entries [0].list [0].start)),
      roundDown (new Date (entries [entries.length - 1].list [0].start))
    ];
    const xRange = [0, dashWidth - 50];
    const yDomain = [
      0,
      Math.max (...Object.values (lists).map (arr => Math.max (...arr.map (el => el.count))))
    ]
    const yRange = [dashHeight / 2, 0]
    // create x-axis
    let xAxisScale = d3.scaleTime ()
      .domain (xDomain)
      .range (xRange);
    let x_axis = d3.axisBottom ().scale (xAxisScale).ticks (log2 (entries.length));
    svg.append ('g').attr ('transform', `translate(35, ${dashHeight-25})`).call (x_axis);
    // create y-axis
    let yAxisScale = d3.scaleLinear ()
      .domain (yDomain)
      .range (yRange);
    let y_axis = d3.axisLeft ().scale (yAxisScale).ticks (log2 (yDomain [1]));
    svg.append ('g').attr ('transform', `translate(35, ${dashHeight / 2 - 25})`).call (y_axis);
    // add legend
    let texts = svg.selectAll ('.legend')
      .data (Object.keys (lists))
      .enter ()
      .append ('text')
      .text (d => d)
      .attr ('class', (d, i) => `trendline-${i + 1}`);
    // remove on click
    texts.on ('click', (d) => {
      let word = d.target.__data__;
      console.log (`removing keyword ${word}`)
      removeKeyword (word) ()
    });
    // create all trend lines
    Object.keys (lists).map ((key, listIndex) => {
      let data = lists [key];
      for (let i = 0; i < data.length - 1; i++) {
        let line = svg.append ('line').attr ('class', `trendline trendline-${listIndex + 1}`);
        line.attr ('transform', `translate(35, ${dashHeight / 2 - 25})`)
          .attr ('x1', xAxisScale (data [i].date))
          .attr ('x2', xAxisScale (data [i + 1].date))
          .attr ('y1', yAxisScale (data [i].count))
          .attr ('y2', yAxisScale (data [i + 1].count));
      }
    });
  }, [keywords, metric]);

  const touchstart = e => {
    setTouchStart (e.changedTouches [0]);
  }

  const touchend = e => {
    if (!tstart) return;
    let xmove = e.changedTouches [0].pageX - tstart.pageX;
    let ymove = e.changedTouches [0].pageY - tstart.pageY;
    setTouchStart (null);
    if (Math.abs (xmove) > Math.abs (ymove)) {
      e.preventDefault ();
      return xmove > 0 ? 
        setMetric ((metric !== 0 ? metric : metrics.length) - 1)
        :
        setMetric ((metric + 1) % metrics.length);
    } else {
      return;
    }
  }

  if (!indexed) return null;
  let metrics = [
    // time of day bar chart
    (
      <figure className="time-of-day">
        <figcaption>Entries By Time Of Day</figcaption>
        <svg key='time-of-day-svg' viewBox={`0 0 ${dashWidth} ${dashHeight}`} xmlns="http://www.w3.org/2000/svg">
          {/** axis */}
          <line x1={dashWidth / 20} y1={dashHeight - 20} x2={dashWidth - dashWidth / 20} y2={dashHeight - 20} />
          {/* hour based bars */}
          {
            indexed.map ((time, i) => <Bar time={time} i={i} dashHeight={dashHeight} dashWidth={dashWidth} hourlyMax={hourlyMax}/>)
          }
        </svg>
      </figure>
    ),
    // type of entry pie chart
    (
      <figure className="type-of-entry">
        <figcaption>Entries By Type</figcaption>
        <svg key='type-of-entry-svg' className="type-of-entry" ref={canvasRef} viewBox={`0 0 ${dashWidth} ${dashHeight}`} xmlns="http://www.w3.org/2000/svg"/>
      </figure>
    ),
    // keyword trendline chart
    (
      <figure className="keywords">
        <figcaption>Keyword Search</figcaption>
        <section className="keyword-container">
          <form onSubmit={addKeyWord} className="keyword-search">
            <input disabled={keywords.length > 3} name="keyword" placeholder="keyword" />
            <button role="submit"><img src={SearchIcon} /></button>
          </form>
        </section>
        <svg ref={wordcountref} viewBox={`0 0 ${dashWidth} ${dashHeight}`} key='keyword-chart' />
      </figure>
    )
  ]
  // return the dashboard
  return (
    <div onTouchStart={touchstart} onTouchEnd={touchend} className="dashboard">
      <div className="arrow" >
        <span onClick={() => setMetric ((metric !== 0 ? metric : metrics.length) - 1)}>
          <img src={Back} />
        </span>
      </div>
      {
        dashWidth > 0 &&
        metrics [metric]
      }
      <div className="arrow">
        <span onClick={() => setMetric ((metric + 1) % metrics.length)}>
          <img src={Next} />
        </span>
      </div>
    </div>
  );
}
